/*
 * Content script that loads on very early on start. If tab is in the checkout process shows the overlay.
 */
import $ from 'jquery'
import template from 'templates/checkout_lock.hbs'
import 'checkout_lock.scss'
import { BACKGROUND_EVENTS } from 'const'

function injectLock () {
  if ($('body.hide-lock').length) return
  if ($('.checkout-lock').length) return

  setTimeout(function () {
    $(template()).appendTo($('body'))
    setTimeout(injectLock, 50)
  }, 1)
}

chrome.runtime.sendMessage({ topic: BACKGROUND_EVENTS.CHECKOUT_LOCK }, function ({ setLock }) {
  if (!setLock) return

  injectLock()
})
